.main-screen {
  margin-top: -120px;
  min-height: 878px;
  background: url(../../img/background.png) repeat, url(../../img/circuits.png) no-repeat 85% 70% $color_blue_green_dark;
  padding-top: 316px;
  box-sizing: border-box;
}

.main-screen__wrapper {
  max-width: 1411px;
  padding: 0 25px;
  margin: 0 auto;
}

.main-screen__title,
.main-screen__content {
  @include reset();
  color: $text_color_sub;
}

.main-screen__title {
  margin-bottom: 27px;
  max-width: 720px;
  font-family: $font_family_alt;
  font-weight: $font_weight_bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
}

.main-screen__content {
  position: relative;
  max-width: 650px;
  margin-left: 98px;
  margin-bottom: 43px;
  font-size: 22px;
  line-height: 34px;
}

.main-screen__content::before {
  content: "";
  position: absolute;
  top: 15px;
  left: -95px;
  width: 66px;
  height: 2px;
  background-color: $color_white;
}

.main-screen__btn {
  margin-left: 98px;
  padding-top: 17px;
  padding-bottom: 17px;
  width: 407px;
  background-color: $background_color_element;
  color: $text_color_sub;
  font-size: 16px;
  line-height: 19px;
}

.main-screen__btn--short {
  display: none;
}

@media (max-width: 1023px) {
  .main-screen {
    margin-top: -94px;
    padding-top: 189px;
    min-height: 686px;
    background: url(../../img/background.png) no-repeat, url(../../img/circuits.png) no-repeat 166% 33% $color_blue_green_dark;
    background-size: cover, 60%;
  }

  .main-screen__wrapper {
    padding: 0 88px;
  }

  .main-screen__title {
    max-width: 500px;
    font-size: 60px;
    line-height: 70px;
  }

  .main-screen__content {
    max-width: 400px;
    margin-left: 51px;
    margin-bottom: 56px;
    font-size: 18px;
    line-height: 28px;
  }

  .main-screen__content::before {
    width: 33px;
    top: 17px;
    left: -48px;
  }

  .main-screen__btn {
    margin-left: 51px;
    width: 379px;
  }
}

@media (max-width: 767px) {
  .main-screen {
    margin-top: -72px;
    padding-top: 286px;
    min-height: 647px;
    background: url(../../img/background.png) no-repeat, url(../../img/circuits.png) no-repeat 161% 22% $color_blue_green_dark;
    background-size: cover, 95%;
  }

  .main-screen__wrapper {
    padding: 0 14px;
  }

  .main-screen__title {
    width: 300px;
    font-size: 40px;
    line-height: 47px;
  }

  .main-screen__content {
    margin-bottom: 40px;
    max-width: 280px;
    font-size: 16px;
    line-height: 25px;
  }

  .main-screen__content::before {
    top: 15px;
  }

  .main-screen__btn {
    margin-left: 0;
    width: 100%;
  }

  .main-screen__btn--long {
    display: none;
  }

  .main-screen__btn--short {
    display: block;
  }
}
