.modal-cart {
  position: relative;
  margin: 0 auto;
  padding: 30px 40px;
  width: 490px;
  background-color: $color_blue;
  color: $color_white;
  box-sizing: border-box;
  display: none;
}

.modal-cart legend {
  margin-bottom: 12px;
  font-size: 28px;
  line-height: 39px;
}

.modal-cart__form-subtitle {
  margin-bottom: 30px;
}

.modal-cart textarea {
  min-height: 104px;
}

.modal-cart button[type="submit"] {
  margin-bottom: 39px;
}

.modal-cart__btn--close {
  position: absolute;
  top: 20px;
  right: 20px;
  margin: 0;
  padding: 0;
  font-size: 0;
  width: 17px;
  height: 17px;
  border: none;
  background-color: transparent;
}

.modal-cart__btn--close::before {
  position: absolute;
  top: 8px;
  left: -3px;
  content: "";
  display: block;
  width: 22px;
  height: 1px;
  background-color: white;
  transform: rotate(-45deg);
}

.modal-cart__btn--close::after {
  position: absolute;
  top: 8px;
  left: -3px;
  content: "";
  display: block;
  width: 22px;
  height: 1px;
  background-color: white;
  transform: rotate(45deg);
}
